import React, { Component } from 'react';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';

import Plot from 'react-plotly.js';

import vizSubTypes from '../Enums/visualizationSubTypes';
import { Paper, Typography } from '@material-ui/core';

import colors from '../Enums/colors';

const mapStateToProps = (state, ownProps) => ({
    charts: state.charts
})

const mapDispatchToProps = {

}

const styles = (theme) => ({
    chartsContainer: {
        margin: '260px 0 0 100px'
    },

    chartWrapper: {
        display: 'inline-block',
        backgroundColor: colors.backgroundGray,
        boxShadow: "0px 6px 6px -3px rgba(0,0,0,0.2),0px 10px 14px 1px rgba(0,0,0,0.14),0px 4px 18px 3px rgba(0,0,0,0.12)",
        margin: '20px',
        color: 'white',
        backgroundColor: colors.backgroundGray
    },

    citation: {
        position: 'relative',
        bottom: '20px',
        right: '9px',
        display: 'table',
        margin: '0 auto 0 auto'
    }
})

const handleContourMap = (chart) => ({
    parameters: chart.parameters,
    metadata: chart.metadata,
    subType: chart.subType,
    data:[
        {
            x: chart.data.map(row => row.lon),
            y: chart.data.map(row => row.lat),
            z: chart.data.map(row => row[chart.parameters.fields]),
            name: chart.parameters.fields,
            type: 'contour',
            contours: {
                coloring: 'heatmap',
                showlabels: true,
                labelfont: {
                    family: 'Raleway',
                    size: 12,
                    color: 'white',
                }
            },
            colorbar: {
                title: {
                    text: `${chart.metadata.unit}`
                }
            }
        }
    ],
    layout: {
        xaxis: {title: 'Longitude'},
        yaxis: {title: 'Latitude'}
    }
})

const handleHistogram = (chart) => ({     
        parameters: chart.parameters,
        metadata: chart.metadata,   
        subType: chart.subType,
        data: [
            {
            x: chart.data.map(row => row[chart.parameters.fields]),
            name: chart.parameters.fields,
            type: 'histogram',
            marker: {color: '#17becf'}
            }
        ],
        layout: {
            xaxis: {title: `${chart.metadata.unit}`}
        }          
})

const handleTimeSeries = (chart) => ({
    parameters: chart.parameters,
    metadata: chart.metadata,
    subType: chart.subType,
    data: [
      {
      x: chart.data.map(row => row.time || row.month),
      y: chart.data.map(row => row[chart.parameters.fields]),
      error_y: {
        type: 'data',
        array: chart.data.map(row => row[chart.parameters.fields + '_std']),
        opacity: 0.2,
        color: 'gray',
        visible: true
      },
      name: chart.parameters.fields,
      type: 'scatter',
      line: {color: '#e377c2'},
      },
    ],
    layout: {
      xaxis: {title: chart.data[0].time ? 'Time' : 'Month'},
      yaxis: {title: chart.parameters.fields}
    }
  })

const handleSectionMap = (chart) => ({
    parameters: chart.parameters,
    metadata: chart.metadata,
    subType: chart.subType,
    data: [
      {
      x: chart.data.map(row => row.lat),
      y: chart.data.map(row => row.depth),
      z: chart.data.map(row => row[chart.parameters.fields]),
      name: chart.parameters.fields,
      type: 'heatmap',
      }
    ],
    layout: {
      xaxis: {title: 'Latitude'},
      yaxis: {autorange: 'reversed', title: 'Depth [m]'}
    }
  })

const handleDepthProfile = (chart) => ({
    parameters: chart.parameters,
    metadata: chart.metadata,
    subType: chart.subType,
    data: [
      {
        x: chart.data.map(row => row.depth),
        y: chart.data.map(row => row[chart.parameters.fields]),
        error_y: {
          type: 'data',
          array: chart.data.map(row => row[chart.parameters.fields] + '_std'),
          opacity: 0.2,
          color: 'gray',
          visible: true
        },
        name: chart.parameters.fields,
        type: 'scatter',
        line: {color: '#e377c2'},
        },
    ],
    layout: {
      xaxis: {title: 'Depth [m]'},
      yaxis: {title: chart.parameters.fields}
    }
  })

const handleGeospatialMap = (chart) => ({
    parameters: chart.parameters,
    metadata: chart.metadata,
    subType: chart.subType,
    data:[
        {
            x: chart.data.map(row => row.lon),
            y: chart.data.map(row => row.lat),
            z: chart.data.map(row => row[chart.parameters.fields]),
            name: chart.parameters.fields,
            type: 'heatmap',
            colorbar: {
                title: {
                    text: `${chart.metadata.unit}`
                }
            }
        }
    ],
    layout: {
        xaxis: {title: 'Longitude'},
        yaxis: {title: 'Latitude'}
    }    
})

class Charts extends Component {

    render(){

        const { classes, charts } = this.props;

        const processedCharts = charts.map(chart => {
            switch(chart.subType){
                case vizSubTypes.contourMap:
                    return handleContourMap(chart);
                case vizSubTypes.histogram:
                    return handleHistogram(chart);
                case vizSubTypes.timeSeries:
                    return handleTimeSeries(chart);
                case vizSubTypes.sectionMap:
                    return handleSectionMap(chart);
                case vizSubTypes.depthProfile:
                    return handleDepthProfile(chart);
                case vizSubTypes.geospatialMap:
                    return handleGeospatialMap(chart);
                default:
                    console.log(`Subtype not found: ${chart.subType}`);
                    return null;
            }
        })

        return (
            <div className={classes.chartsContainer}>
                {processedCharts.map((chart, index) => (
                    <div key={index} className={classes.chartWrapper}>
                        <Plot
                            style= {{
                                position: 'relative',
                                display:'inline-block'
                            }}
                            key={index}
                            layout= {{...chart.layout,

                                title: `${chart.parameters.fields} - ${chart.subType} - ${chart.parameters.dt1} - ${Number(chart.parameters.depth1) || 'Surface'}`,
                                font: {... chart.layout.font,
                                    color: '#ffffff'
                                },
    
                                xaxis: {...chart.layout.xaxis,
                                    color: '#ffffff'
                                },
    
                                yaxis: {...chart.layout.yaxis,
                                    color: '#ffffff'
                                },
    
                                paper_bgcolor: colors.backgroundGray,

                                annotations: [
                                    {
                                        text: `Source: ${chart.metadata.distributor.length < 30 ? 
                                                chart.metadata.distributor : 
                                                chart.metadata.distributor.slice(0,30)} -- Provided by Simons CMAP`,
                                        font: {
                                            color: 'white',
                                            size: 10
                                        },
                                        xref: 'paper',
                                        yref: 'paper',
                                        yshift: -202,
                                        showarrow: false,
                                    }
                                ]
                            }}
                            data={chart.data}
                        />
                        {/* <Typography className={classes.citation} align='center' variant='caption'>Test</Typography> */}
                    </div>                    
                ))}
            </div>
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Charts));